import axios from "axios";
import Cookies from "js-cookie";
// 
export const ERR_OK = 8200
// 
export const TIME_QUERY = 500

export const imageCDN = process.env.IMAGE_CDN
export const baseUrl = "http://192.168.0.252:8001";
export const baseImgUrl = process.env.BASE_URL

// 
axios.defaults.timeout = 5000

// respone
axios.interceptors.response.use(
  response => {
    /**
     * responsecodecode
     * xmlhttprequest  error
     */
    let {
      data
    } = response
    console.log(data.message)
    if (data.message === 'token failure!') {
      localStorage.removeItem('token')
      localStorage.removeItem('UserName')
      localStorage.removeItem('Password')
      localStorage.removeItem('token')
      window.location.href = '#/'
    } else {
      return Promise.resolve(response)
    }
    // 50014:Token  50012: 50008:token
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export function fetch(requestUrl, params = '', type = 'post', useData = true, isDownload = false) {
  var qs = require('qs');
  const token = localStorage.getItem('token') === null ? '' : localStorage.getItem('token')
  if (type === 'post') {
    return axios({
      url: requestUrl,
      method: 'post',
      data:  useData ? params : undefined,
      params: useData ? undefined : params,
      headers: {
        'Content-Type':  isDownload ?  'application/json; application/octet-stream': 'application/json;charset=UTF-8',
        'Access-Control-Allow-Origin': '*',
        'Authorization': token
      },
      responseType: isDownload?  'blob' : ''
    })
  } else {
    return axios({
      url: requestUrl,
      method: type,
      params: params,
      paramsSerializer: params => {
        return qs.stringify(params, { indices: false })
      },
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'Access-Control-Allow-Origin': '*',
        'Authorization': token
      }
    })
  }
}


export function fetchapi(requestUrl, params = '') {
  const token = localStorage.getItem('token') === null ? '' : localStorage.getItem('token')
  return axios({
    url: `${baseUrl}api/standardCheck`,
    method: 'post',
    data: {
      'requestId': 'xxxxxx-xxxxxx-xxxxx-xxxxx',
      'apiKey': 'highstore',
      'uri': requestUrl,
      'lang': 'zh-cn',
      'timeZone': '+8:00',
      'sign': ' xxxxxxxxxxxxxxxxxxxxxx',
      'body': params
    },
    headers: {
      'Content-Type': 'application/json;charset=UTF-8',
      'Authorization': token
    }
  })
}


export function getCookies(params) {
  return Cookies.get(params) || ''
}

export function setCookies(memberNo, openid, token) {
  Cookies.set('memberNo', memberNo)
  Cookies.set('openid', openid)
  Cookies.set('token', token)
}



