import { fetch, baseUrl } from "./gocenthttpclient.js";

/**
 * 
 * @param params
 * @returns {*}
 */
export function AllDeptInfo(params) {
    return fetch(`${baseUrl}/api/Schedual/AllDeptInfo`, params, "GET");
}

/**
 * 
 * @param params
 * @returns {*}
 */
export function AllDocInfo(params) {
    return fetch(`${baseUrl}/api/Schedual/GetDocInfo`, params, "GET");
}


/**
 * 
 * @param params
 * @returns {*}
 */
export function AllSchedual(params) {
    return fetch(`${baseUrl}/api/Schedual/SchedualInfo`, params, "GET");
}

/**
 * 
 * @param params
 * @returns {*}
 */
export function AllCharge(params) {
    return fetch(`${baseUrl}/api/ChargeHandler/GetChargeList`, params, "GET");
}


