<template>
  <div id="buttomcss">
    <tab-bar>
      <tab-bar-item-left path="/home">
        <img
          style="height:90%"
          slot="item-icon" lazy="loading"
          src="../../assets/img/iconfont/hospital_info.png"
          alt=""
        />
        <img
          style="height:90%"
          slot="item-icon-active" lazy="loading"
          src="../../assets/img/iconfont/hospital_info_active.png"
          alt=""
        />
        <div slot="item-text"></div>
      </tab-bar-item-left>
      <tab-bar-item path="/shopcart">
        <img
          style="height:100%"
          slot="item-icon" lazy="loading"
          src="../../assets/img/iconfont/hospital_server.png"
          alt=""
        />
        <img
          style="height:100%"
          slot="item-icon-active" lazy="loading"
          src="../../assets/img/iconfont/hospital_server_active.png"
          alt=""
        />
        <div slot="item-text"></div>
      </tab-bar-item>
      <tab-bar-item-right path="/profile">
        <img
          style="height:90%"
          slot="item-icon" lazy="loading"
          src="../../assets/img/iconfont/my_info.png"
          alt=""
        />
        <img
          style="height:90%"
          slot="item-icon-active" lazy="loading"
          src="../../assets/img/iconfont/my_info_active.png"
          alt=""
        />
        <div slot="item-text"></div>
      </tab-bar-item-right>
    </tab-bar>
  </div>
</template>
<script>
import TabBar from "./tabbar";
import TabBarItem from "./tabbarItem";
import TabBarItemLeft from "./tabbarItem_left";
import TabBarItemRight from "./tabbarItem_right";

export default {
  name: "buttomcss",
  components: {
    TabBar,
    TabBarItem,
    TabBarItemLeft,
    TabBarItemRight
  }
};
</script>
<style>
@import "../../assets/css/base.css";
.active {
  color: blue;
}
</style>



